/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { graphql } from 'gatsby';
import Layout from 'components/Layout';
import SEO from 'components/SEO';
import PostCard from 'components/Cards/PostCard';
import CategoriesCard from 'src/components/Cards/CategoriesCard';
import { Fragment } from 'react';
import { Box } from '@material-ui/core';
import TocCard from 'src/components/Cards/TocCard';
import PostNavigatorCard from 'src/components/Cards/PostNavigatorCard';

interface Props {
  data: any;
  pageContext: {
    previous: any;
    next: any;
  };
  location: Location;
}

const PostTemplate = ({ data, pageContext, location }: Props) => {
  const { frontmatter, excerpt, html, tableOfContents } = data.markdownRemark;
  const { previous, next } = pageContext;
  const thumbnail = frontmatter.thumbnail.childImageSharp.fluid;

  return (
    <Layout
      location={location}
      right={
        <Fragment>
          <Box
            css={css`
              margin-bottom: 1rem;
            `}
            display={{ xs: 'none', lg: 'block' }}
          >
            <TocCard tocHtml={tableOfContents} />
          </Box>
          <Box>
            <CategoriesCard />
          </Box>
        </Fragment>
      }
      rightSticky={true}
    >
      <SEO title={frontmatter.title} description={frontmatter.description ?? excerpt} image={thumbnail?.src} />
      <Box
        css={css`
          margin-bottom: 2rem;
        `}
      >
        <PostCard
          title={frontmatter.title}
          date={frontmatter.date}
          thumbnail={thumbnail}
          html={html}
          categories={frontmatter.categories}
          tags={frontmatter.tags}
        />
      </Box>
      <PostNavigatorCard prev={previous} next={next} />
    </Layout>
  );
};

export default PostTemplate;

export const pageQuery = graphql`
  query PostBySlugQuery($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(truncate: true)
      html
      tableOfContents(absolute: false, maxDepth: 3)
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD")
        categories
        tags
        thumbnail {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;
