/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Card, CardContent, Grid, Chip } from '@material-ui/core';
import { FluidObject } from 'gatsby-image';
import PostHeader from 'components/PostHeader';
import Markdown from 'components/Markdown';
import RelatedPosts from 'components/RelatedPosts';
import * as Icon from 'react-feather';

interface Props {
  title: string;
  thumbnail: FluidObject;
  date: string;
  html: string;
  categories: string[];
  tags: string[];
}
const PostCard = ({ title, html, date, thumbnail, categories, tags }: Props) => {
  return (
    <Card>
      <Thumbnail>
        <img src={thumbnail.src} alt={title} title={title} />
      </Thumbnail>
      <CardContent component="article">
        <PostHeader title={title} date={date} categories={categories} />
        <Markdown html={html} />
        <RelatedPosts
          css={css`
            margin: 2rem 0;
          `}
          tags={tags}
          currentPost={title}
        />
        <Grid
          container
          css={css`
            margin-top: 2rem;
          `}
          component="ul"
          spacing={1}
        >
          <Grid
            item
            component="li"
            css={css`
              list-style: none;
              margin: 0;
              padding: 0;
            `}
          >
            <Icon.Tag />
          </Grid>
          {tags.map((tag, index) => (
            <Grid
              item
              component="li"
              key={index}
              css={css`
                list-style: none;
                margin: 0;
                padding: 0;
              `}
            >
              <Chip label={tag} size="small" />
            </Grid>
          ))}
        </Grid>
      </CardContent>
    </Card>
  );
};

export default PostCard;

const Thumbnail = styled.div`
  padding-top: 42.8%;
  display: block;
  position: relative;
  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: 0;
  }
`;
