/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { Card, Box, CardActionArea, CardContent, useTheme } from '@material-ui/core';
import { Post } from 'src/models/post';
import { Link } from 'gatsby';
import styled from '@emotion/styled';
import { Colors } from 'src/utils/colors';

interface Props {
  prev?: Post | null;
  next?: Post | null;
}
const PostNavigatorCard = ({ prev, next }: Props) => {
  const theme = useTheme();
  return (
    <Box
      component="nav"
      display="flex"
      css={css`
        flex-wrap: wrap;
        justify-content: space-between;
      `}
    >
      {next ? (
        <Card
          css={css`
            width: 100%;
            margin-bottom: 1rem;
            ${theme.breakpoints.up('lg')} {
              width: auto;
              margin-bottom: 0;
            }
          `}
        >
          <PostLink to={next.fields.path ?? '/'}>
            <CardActionArea>
              <CardContent>
                <span role="img" aria-label="previous">
                  
                </span>{' '}
                {next.frontmatter.title}
              </CardContent>
            </CardActionArea>
          </PostLink>
        </Card>
      ) : (
        <div />
      )}
      {prev ? (
        <Card
          css={css`
            width: 100%;
            text-align: right;
            ${theme.breakpoints.up('lg')} {
              width: auto;
            }
          `}
        >
          <PostLink to={prev.fields.path ?? '/'} rel="prev">
            <CardActionArea>
              <CardContent>
                {prev.frontmatter.title}{' '}
                <span role="img" aria-label="next">
                  
                </span>
              </CardContent>
            </CardActionArea>
          </PostLink>
        </Card>
      ) : (
        <div />
      )}
    </Box>
  );
};

export default PostNavigatorCard;

const PostLink = styled(Link)`
  color: ${Colors.Grey['600']};
  text-decoration: none;
`;
